@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;800&display=swap);
.theme-switch{position:relative}.theme-switch__checkbox{-webkit-appearance:none;appearance:none;position:absolute;top:0;left:0;width:100%;height:100%;cursor:pointer}.theme-switch__label{display:flex;font-size:1.4rem;align-items:center;grid-gap:.6rem;gap:.6rem}@media screen and (max-width: 28em){.theme-switch__text{display:none}}
.header{background-color:var(--element-color);box-shadow:var(--shadow);display:flex;grid-gap:2.4rem;gap:2.4rem;align-items:center;justify-content:space-between;padding:2.8rem 7.8rem}.header__title{font-size:2.2rem}@media screen and (max-width: 28em){.header__title{font-size:2rem}}@media screen and (max-width: 55em){.header{padding:2.8rem 4.8rem}}@media screen and (max-width: 25em){.header{padding:2.8rem}}
.search-bar{position:relative;flex:0 1 48rem}@media screen and (max-width: 40em){.search-bar{flex:1 1 100%}}.search-bar__icon{position:absolute;z-index:10;top:50%;left:2.4rem;transform:translate(-50%, -50%);color:var(--text-color);font-size:1.6rem}.search-bar__input{width:100%;font-size:1.4rem;height:5.6rem;background-color:var(--element-color);box-shadow:var(--shadow);border:none;border-radius:.5rem;padding:0 2.4rem 0 4.8rem}.search-bar__input:focus{outline:none}.search-bar__input:-ms-input-placeholder{color:var(--placeholder-color)}.search-bar__input::placeholder{color:var(--placeholder-color)}
.rs{width:20rem;flex-shrink:0}.rs .rs__control{background-color:var(--element-color);box-shadow:var(--shadow);border:none;border-radius:.5rem;cursor:pointer;height:5.6rem;transition:none}.rs .rs__value-container{padding:0 2.4rem}.rs .rs__single-value{color:var(--text-color);font-size:1.4rem}.rs .rs__placeholder{color:var(--placeholder-color);font-size:1.4rem}.rs .rs__indicator{color:var(--placeholder-color)}.rs .rs__indicator:hover{color:var(--text-color)}.rs .rs__indicator-separator{opacity:.5;background-color:var(--placeholder-color)}.rs .rs__menu{background-color:var(--element-color);box-shadow:var(--shadow);border-radius:.5rem;margin:.5rem 0;font-size:1.4rem}.rs .rs__menu-list{max-height:30rem;padding:1rem 0}.rs .rs__option{padding:.5rem 2.4rem;color:var(--text-color);cursor:pointer}.rs .rs__option--is-focused{background-color:var(--hightlight-color)}.rs .rs__option:active,.rs .rs__option--is-selected{background-color:var(--focus-color)}
.country{width:26rem;height:34rem;display:flex;flex-direction:column;border-radius:.5rem;overflow:hidden;background-color:var(--element-color);box-shadow:var(--shadow)}.country__flag-container{height:16rem}.country__flag{height:100%;width:100%;object-fit:cover;object-position:center;box-shadow:var(--shadow)}.country__info{flex:auto;padding:2.4rem;display:flex;flex-direction:column;grid-gap:.6rem;gap:.6rem}.country__name{font-size:1.6rem;margin-bottom:1.2rem;font-weight:800}.country__info-item{display:flex;grid-gap:.4rem;gap:.4rem}.country__label{font-size:1.4rem;font-weight:600}.country__value{font-size:1.4rem;font-size:300}
.country-list{list-style:none;display:grid;grid-template-columns:repeat(auto-fit, minmax(26rem, 1fr));grid-gap:7.5rem;gap:7.5rem;justify-items:center}@media screen and (max-width: 45em){.country-list{grid-gap:4.8rem;gap:4.8rem}}
.home__bar{display:flex;justify-content:space-between;margin-bottom:4.8rem;grid-gap:2.4rem;gap:2.4rem}@media screen and (max-width: 40em){.home__bar{flex-wrap:wrap}}
.back-btn{background-color:var(--element-color);box-shadow:var(--shadow);font-size:1.6rem;display:inline-flex;align-items:center;justify-content:center;grid-gap:1rem;gap:1rem;padding:.8rem 3.2rem;border-radius:.5rem}.back-btn__icon{font-size:2.4rem}
.details__country{margin-top:4.8rem;display:grid;grid-template-columns:repeat(2, 1fr);grid-template-rows:1fr;grid-gap:12.8rem;gap:12.8rem;align-items:center;justify-items:center}.details__country-flag{width:100%;box-shadow:var(--shadow)}@media screen and (max-width: 70em){.details__country{grid-gap:7.8rem;gap:7.8rem}}@media screen and (max-width: 50em){.details__country{grid-template-columns:1fr}.details__country-flag{width:70%}}@media screen and (max-width: 45em){.details__country{grid-gap:4.8rem;gap:4.8rem}.details__country-flag{width:80%}}@media screen and (max-width: 40em){.details__country-flag{width:90%}}@media screen and (max-width: 35em){.details__country{justify-items:flex-start}.details__country-flag{width:100%}}.details__name{font-size:3rem}.details__info-items{margin-top:2.4rem;display:grid;grid-template-columns:repeat(2, 1fr);grid-gap:1rem 3rem;gap:1rem 3rem}@media screen and (max-width: 60em){.details__info-items{grid-template-columns:1fr}}@media screen and (max-width: 50em){.details__info-items{grid-template-columns:repeat(2, 1fr)}}@media screen and (max-width: 35em){.details__info-items{grid-template-columns:1fr}}.details__info-item--subregion{grid-column:1/span 2}@media screen and (max-width: 60em){.details__info-item--subregion{grid-column:1}}.details__info-item--borders{margin-top:7.2rem;display:flex;flex-wrap:wrap;grid-gap:1rem;gap:1rem;align-items:center}.details__label{font-size:1.6rem;font-weight:600;white-space:nowrap;display:inline}.details__value{font-size:1.6rem;font-weight:300;display:inline}.details__border-country{display:inline-block;font-size:1.4rem;padding:.6rem 1.4rem;background-color:var(--element-color);box-shadow:var(--shadow);border-radius:.5rem}.details__container{display:flex;align-items:center;justify-content:center;height:100%}.details__not-found-title{font-size:2.4rem;margin-bottom:6rem}
.not-found__container{display:flex;align-items:center;justify-content:center;height:100%}.not-found__title{font-size:2.4rem;margin-bottom:6rem}
.app{--element-color: hsl(0, 0%, 100%);--background-color: hsl(0, 0%, 98%);--hightlight-color: hsl(0, 0%, 88%);--focus-color: hsl(0, 0%, 83%);--text-color: hsl(200, 15%, 8%);--placeholder-color: hsl(200, 15%, 32%);--shadow: 0 0 0.5rem 0.5rem rgba(0, 0, 0, 0.05);background-color:var(--background-color);color:var(--text-color);min-height:100vh;overflow:hidden;display:grid;grid-template-rows:-webkit-min-content 1fr;grid-template-rows:min-content 1fr}.app.dark{--element-color: hsl(209, 23%, 22%);--background-color: hsl(207, 26%, 17%);--hightlight-color: hsl(209, 23%, 32%);--focus-color: hsl(209, 23%, 38%);--text-color: hsl(0, 0%, 100%);--placeholder-color: hsl(0, 0%, 80%)}*{margin:0;padding:0}*,*::before,*::after{box-sizing:inherit}html{box-sizing:border-box;font-size:62.5%}@media screen and (max-width: 25em){html{font-size:50%}}body{font-family:"Nunito Sans",sans-serif}a,input,button{font-family:"Nunito Sans",sans-serif;text-decoration:none;color:var(--text-color)}.page{padding:4.8rem 7.8rem 7.8rem;height:100%;display:grid}.page__container{display:flex;align-items:center;justify-content:center}.page__not-found-title{font-size:2.4rem;margin-bottom:6rem}@media screen and (max-width: 55em){.page{padding:4.8rem}}@media screen and (max-width: 25em){.page{padding:2.8rem}}
